import { createWebHistory, createRouter } from "vue-router";
import Product from './components/ProductWrap.vue'
import Company from './components/CompanyWrap.vue'
import Service from './components/ServiceWrap.vue'
import Game from './components/GameWrap.vue'

const routes = [
    {
        path: "/",
        component: Product
    },
    {
        path: "/company",
        component: Company
    },
    {
        path: '/service',
        component: Service
    },
    {
        path: '/game',
        component: Game
    }
];

const router = createRouter({
    history: createWebHistory(),
    routes
})

export default router;