<template lang="">
    <div id="container">
        <h2>racket list</h2>
        <div class="in_wrap">
            <div class="content" v-for="(c, i) in datas" :key="i">
                <h3 @click="modalOpen(i)">{{c.title}}</h3>
                <h5 @click="modalOpen(i)">{{c.price}}원</h5>
                <img @click="modalOpen(i)" :src="c.image" :alt="c.id">
                <Modal :modal="modal" :data="datas" :crt="crt" @closeModal="modalClose"></Modal>
            </div>
        </div>
    </div>

</template>
<script>
import data from '../assets/data'
import Modal from './ModalWrap'

export default {
    data() {
        return {
            datas: data,
            modal: false,
            crt: 0
        }
    },
    components: {
        Modal
    },
    methods: {
        modalOpen(i) {
            this.crt = i;
            this.modal = true;
        },
        modalClose() {
            this.modal = false;
        }
    },
}
</script>
<style scoped>
#container {
 margin-bottom: 10rem
 }
 .in_wrap {
 justify-content: space-around;
 display: flex;
 flex-wrap: wrap;
 cursor: pointer;
 }
 img {
 /* width: auto; */
 height: 200px;
 }
 h3 {
 color: gray;
 font-size: 1.6rem;
 margin: 3rem 0 1rem;
 position: relative
 }
 h3:after {
 display: block;
 content: "";
 width: 10%;
 height: .5rem;
 background-color: gainsboro;
 margin: 1rem auto
 }
 .more {
    border: none;
    color: rgb(77, 77, 77);
    border-radius: 2rem;
    padding: 0 1rem;
    display: inline-block;
    margin-top: 3rem;
    line-height: 3rem;
    cursor: pointer;
 }
</style>