<template lang="">
    <div id="container">
        <h2>구구단</h2>
        <div>
            <h3> {{first}} 곱하기 {{second}}</h3>
            <form action="">
                <input type="number" v-model="value">
                <button type="submit" @click.prevent="onSubmit" @keyup.enter="onSubmit">
                    입력
                </button>
            </form>
            <div id="result">
                {{result}}
            </div>
        </div>
        <br><br>
        <h2>끝말잇기</h2>
        <h3>{{word}}</h3>
        <form v-on:submit.prevent="onSubmit2">
            <input type="text" v-model="value2">
            <button type="submit">입력</button>
        </form>
        <div id="result">
            {{result2}}
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            first: 1 + (Math.ceil(Math.random()*8)),
            second: Math.ceil(Math.random()*8),
            result: "",
            value: "",
            word: "시작",
            value2: "",
            result2: ""
        }
    },
    methods: {
        onSubmit() {
            if(this.first*this.second == parseInt(this.value)) {
                this.result = "정답입니다."
                this.value = ""
                this.first = 1 + (Math.ceil(Math.random()*8))
                this.second = Math.ceil(Math.random()*8)
            } else {
                this.result = "틀렸습니다."
                this.value = ""
            }
        },
        onSubmit2() {
            if(this.word[this.word.length-1] === this.value2[0]) {
                this.result2 = "정답"
                this.word = this.value2
                this.value2 = ""
            } else {
                this.result2 = "틀렸습니다"
            }
        }
    },
    
}
</script>
<style scoped>
#container {
margin-bottom: 10rem
}
.in_wrap {
justify-content: space-around;
display: flex;
flex-wrap: wrap;
cursor: pointer;
}
img {
/* width: auto; */
height: 200px;
}
h3 {
color: gray;
font-size: 1.6rem;
margin: 3rem 0 1rem;
position: relative
}
h3:after {
/* display: block; */
content: "";
width: 10%;
height: .5rem;
background-color: gainsboro;
margin: 1rem auto
}
.more {
border: none;
color: rgb(77, 77, 77);
border-radius: 2rem;
padding: 0 1rem;
display: inline-block;
margin-top: 3rem;
line-height: 3rem;
cursor: pointer;
}
</style>